import { Component, Input, OnChanges, SimpleChanges, ViewChild } from '@angular/core';
import { Thin2Chart } from 'thin2-chart';
import { EntityNameDictionary, EntityColorDictionary } from '../../../model/entity.enum';
import { DashboardService } from '../../dashboard.service';

@Component({
    selector: 'pie-chart-entity-component',
    templateUrl: './pie-chart-entity.component.html'
})
export class PieChartEntityComponent implements OnChanges {
    @ViewChild(Thin2Chart)
    public chart: Thin2Chart;

    @Input()
    public dataset: any[];

    constructor(private dashboardService: DashboardService) {
        console.log('constructor');
        console.log(this.dataset);
    }

    // Angular  ngOnChanges 
    //  SimpleChanges 
    public ngOnChanges(changes: SimpleChanges): void {
        this.dataset = changes.dataset.currentValue;

        if (this.dataset) {
            this.dataset = this.dataset.map(this.datasetAdapter);

            // entityentity
            const entityList = this.dashboardService.getLastFilter().entity;

            if (entityList.indexOf('all') < 0) {
                const data = [];

                this.dataset.forEach((v) => {
                    if (entityList.indexOf(v.name.toLowerCase()) >= 0) {
                        data.push(v);
                    }
                });

                this.dataset = data;
            }

            this.drawChart();
        }
    }

    public drawChart() {
        const options = {
            chart: {
                plotBackgroundColor: null,
                plotBorderWidth: null,
                plotShadow: false,
                type: 'pie'
            },
            title: {
                text: ''
            },
            tooltip: {
                formatter() {
                    return `<b>${this.point.name}</b> <br> <b>Total news: ${this.y}</b>`;
                }
            },
            accessibility: {
                point: {
                    valueSuffix: ''
                }
            },
            plotOptions: {
                pie: {
                    allowPointSelect: true,
                    cursor: 'pointer',
                    dataLabels: {
                        enabled: true,
                        formatter() {
                            return `<b>${this.point.name} (${this.y})</b>`;
                        }
                        // format: '<b>SDG: {point.name} ({y})</b>'
                    }
                }
            },
            series: [
                {
                    name: '',
                    data: this.dataset
                }
            ]
        };

        this.chart.draw(options);
    }

    // StatisticsODS
    public datasetAdapter = (item) => ({
        name: item.entity.toLowerCase() === 'others' ? 'Others' : EntityNameDictionary[item.entity],
        color: EntityColorDictionary[item.id],
        y: Number(item.total)
    });
}
